@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}
/*
background-image:url('../../imgs/bg.jpg');
  background:cover;
  background-position:center;
*/
html{
  position:absolute;
  width:100%;
  height:100%;
  overflow:hidden;
  background-color:azure;
}


.App {
  position:absolute;
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  overflow-x:hidden;
  overflow-y:auto;
  text-align: center;
  background-color:azure;
}

.header-btn{
  position:absolute;
  left:0;
  top:0;
}


.header-btn small,.header-btn a,.header-btn button{
  -webkit-text-fill-color:azure;
  color:azure;
}

.header-btn code{
  -webkit-text-fill-color:crimson;
  color:crimson;
}



.pcMsg{
  -webkit-text-fill-color:limegreen;
  color:limegreen;
  font-weight:bolder;
  position:absolute;
  right:20px;
  top:20px;

}



:root {
  --yellow: #feb60a;
  --red: #ff0062;
  --blue: #00dbf9;
  --violet: #da00f7;
}

div.dots div{
  display: flex;
  justify-content: center;
  align-items: center;
}
div.dots div > div {
  width: 1vw;
  height: 1vw;
  border-radius: 100%;
  margin: .5vw;
  background-image: linear-gradient(145deg, rgba(255,255,255,0.5) 0%, rgba(0,0,0,0) 100%);
  -webkit-animation: bounce 1.5s 0.5s linear infinite;
          animation: bounce 1.5s 0.5s linear infinite;
}
.yellow {
  background-color: #feb60a;
  background-color: var(--yellow);
}

.red {
  background-color: #ff0062;
  background-color: var(--red);
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.blue {
  background-color: #00dbf9;
  background-color: var(--blue);
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.violet {
  background-color: #da00f7;
  background-color: var(--violet);
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

@-webkit-keyframes bounce {
  0%, 50%, 100% {
    transform: scale(1);
    -webkit-filter: blur(0px);
            filter: blur(0px);
  }
  25% {
    transform: scale(0.6);
    -webkit-filter: blur(3px);
            filter: blur(3px);
  }
  75% {
    -webkit-filter: blur(3px);
            filter: blur(3px);
    transform: scale(1.4);
  }
}

@keyframes bounce {
  0%, 50%, 100% {
    transform: scale(1);
    -webkit-filter: blur(0px);
            filter: blur(0px);
  }
  25% {
    transform: scale(0.6);
    -webkit-filter: blur(3px);
            filter: blur(3px);
  }
  75% {
    -webkit-filter: blur(3px);
            filter: blur(3px);
    transform: scale(1.4);
  }
}
